@font-face {
  font-family: 'hokjesgeestregular';
  src: url('../fonts/hokjesgeest-webfont.woff2') format('woff2'),
    url('../fonts/hokjesgeest-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

.headerContainer {
  background-image: url('../img/basketball.jpg');
  background-size: cover;
  background-position: 50% 50%;
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  padding: 10px;
  text-align: center;
}

.headerText {
  font-size: 80px;
  font-family: 'hokjesgeestregular', Arial;
  letter-spacing: 5px;
}

@media only screen and (max-width: 891px) {
  .headerText {
    font-size: 60px;
    font-family: 'hokjesgeestregular', Arial;
    letter-spacing: 5px;
  }
}

@media only screen and (max-width: 679px) {
  .headerText {
    font-size: 40px;
    font-family: 'hokjesgeestregular', Arial;
    letter-spacing: 5px;
  }
}

@media only screen and (max-width: 473px) {
  .headerText {
    font-size: 20px;
    font-family: 'hokjesgeestregular', Arial;
    letter-spacing: 5px;
  }
}
